import React from 'react';
import './work.css';

class Work extends React.Component {
    render() {
        return(
            <div id="work" className="work">
                <div className="work-section">
                    <div className="download-img"></div>
                    <div className="content">
                        <h2>Download</h2>
                        <p>Download mobile banking app for iOS and android. It helps you banking quickly and more smartly</p>
                        <button><i className="fab fa-app-store-ios"></i>App Store</button>
                        <button><i className="fab fa-google-play"></i> Play Store</button>
                    </div>
                </div>
                <div className="work-section">
                    <div className="content">
                        <h2>Connecting all your banking needs</h2>
                        <p>financial transaction remotely using a mobile device such as a smartphone or tablet</p>
                        <button>Get Started</button>
                    </div>
                    <div className="connect-img"></div>
                </div>
            </div>
        )
    }
};
export default Work;